@import url(https://fonts.googleapis.com/css?family=Raleway);
html,
body {
  height: 100%;
  width: 100%;
}

body {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  background: #0379e7; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #0379e7 0%,#2989d8 49%,#053263 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #0379e7 0%,#2989d8 49%,#053263 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0379e7', endColorstr='#053263',GradientType=1 ); /* IE6-9 */
  font-family: Raleway;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

p {
  margin: 10px;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

#Body {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
}

#background {
  background-color: #fffefc;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 1px;
          perspective: 1px;
  border-radius: 15px;
  width: 75vw;
  height: 100%;
  padding-top: 25px;
  margin-bottom: 100px;
  margin-top: 100px;
  box-shadow: 0 0 10px #090342;
  color: #454545;
  background-attachment: scroll;
}

.Section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50vw;
  text-align: center;
}

.Section h1 {
  font-size: 3.5em;
  padding-left: 25px;
  padding-right: 25px;
  border-bottom: 3px solid #454545;
}

.Section h2 {
  font-size: 2em;
  border-bottom: 3px solid #454545;
  margin-top: 0;
}

.Section ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
  margin-bottom: 100px;
  margin-top: 0;
  padding: 0;
}

.social {
  width: 50px;
  height: 50px;
  margin: 25px;
  padding: 0;
}

#facebook {
  color: #454545;
}
#facebook:hover {
  color: #4A67AD;
}

#twitter {
  color: #454545;
}
#twitter:hover {
  color: #4CA0EB;
}

#github {
  color: #454545;
}
#github:hover {
  color: #25292E;
}

#linkedin {
  color: #454545;
}
#linkedin:hover {
  color: #3277B0;
}

#emailContact {
  color: black;
  font-weight: bold;
  font-size: 1.25em;
  text-decoration: none;
  margin-bottom: 25px;
}

#emailContact:hover {
  text-decoration: underline;
  color: #454545;
}

.smallCardContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

@media only screen and (max-width: 1100px) {
  .Section {
    width: 65vw;
  }
}

@media only screen and (max-width: 992px) {
  #background {
    width: 85vw;
  }
  .Section {
    width: 75vw;
  }
}

@media only screen and (max-width: 700px) {
  #background {
    width: 100vw;
    margin-bottom: 150px;
    margin-top: 150px;
  }
  .Section {
    width: 90vw;
  }
}

#header {
  display: flex;
  justify-content: flex-start;
  position: absolute;
  z-index: 2;
  margin-left: 25px;
  margin-top: 25px;
}

.Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 750px;
  width: 80vw;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}
.icon {
  margin: 50px;
  box-shadow: 0 0 10px #111;
  border-radius: 20%;
}

.download {
  display: flex;
  flex-direction: column;
}

.download a {
  margin: 10px;
}

.storeLinkApple {
  height: 75px;
  width: auto;
}

.storeLinkGoogle {
  height: 110px;
  width: auto;
}

@media only screen and (max-width: 992px) {
  .Card {
    width: 85vw;
  }
}

@media only screen and (max-width: 768px) {
  .Card {
    width: 100vw;
  }
}

.SmallCard {
  width: 300px;
  height: 400px;
  box-shadow: 2px 2px 10px #555;
  margin: 20px 10px;
}

.SmallCard img {
  width: 300px;
  height: 175px;
  box-shadow: 0 2px 5px #555;
  margin-bottom: 10px;
}

.SmallCard div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.SmallCard a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.SmallCard a:hover {
  color: #454545;
  text-decoration: underline;
}

.cardDescription {
  margin: 8px;
}

.cardDescription p {
  margin: 5px;
}

.SmallCard {
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.SmallCard:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

